import React from 'react'

class Fun extends React.Component {
    constructor() {
        super();
    }

    render() {
        return (
            <section id="fun" style={{ minHeight: '500px' }}>
                <div className="container-fluid">
                    <div className="animate-box section-header" data-animate-effect="fadeInLeft">
                        <span className="font-note">More</span>
                        <h2 className="header">Useful D3 Sites</h2>
                    </div>
                </div>
                <div className="container-fluid">
                D3 official site: <a href='https://d3js.org/' target='_blank'>D3.js - Data-Driven Documents</a>
                <br/>
                A D3 wrapper: <a href='https://vega.github.io/vega/' target='_blank'>Vega – A Visualization Grammar</a>
                <br/>
                Great blog: <a href='https://www.d3-graph-gallery.com/' target='_blank'>The D3 Graph Gallery – Simple charts made with d3.js</a>
                </div>
            </section>
        )
    }
}
export default Fun